.contact{
    margin-top: 40px;
}

.contact h3{
  font-family: 'Slabo 27px', serif;
    font-weight:400;
    /* font-size: 50px; */
    font-size: 2rem;
}

.contact_info{
    display: flex;
    justify-content:center;
    margin-top: 40px;
}

.contact_info h4{
  font-family: 'IBM Plex Mono', monospace;

}

.address {
    border-right: 1px solid gray;
    width: 20%;
    margin-right: 3%;
}

.email{
    border-right: 1px solid gray;
    width: 20%;
    margin-right: 2%;
}

.call{
    border-right: 1px solid gray;
    width: 20%;
    margin-right: 3%;
}

.timing{
    width: 20%;
}

.contact_info h6{
    color: gray;
}

/* Form */

.form{
    margin-top: 10%;
    font-family: 'IBM Plex Mono', monospace;

}

.submit p{
  margin: 0;
  font-family: 'IBM Plex Mono', monospace;
}

