@media screen and (max-width: 950px){
  .contact_info{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .address {
    border-right: none;
    border-bottom: 1px solid gray;
    margin-bottom: 30px;
    width: 50%;
}

  .email{
      border-right: none;
      border-bottom: 1px solid gray;
      margin-bottom: 30px;
      width: 50%;
  }

  .call{
      border-right: none;
      border-bottom: 1px solid gray;
      margin-bottom: 30px;
      width: 50%;
  }

  .timing{
    width: 50%;
  }

  .icon svg{
    width: 50px;
    height: 50px;
    margin-bottom: 10px;
    color: #601616;

  }

}

